@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.entrence{
    width: 100vw;
    height: 100vh;
    background-color: #1c1f1e;
    display: flex;
    justify-content: center;
    align-items: center;
}

.align{
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    animation: reversBlink 4s;
}
.align .boxs {
    width: 25vw;
    height: 60vh;
    
    display: flex;
    flex-direction: column;
}
.align .boxs .box1{
    flex: .5 1;
    background-color: #252827;
    transform: translateY(15vh);
    box-shadow: 0 5px 5px 0 #f98909;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    transition: .5s;
}
.align .boxs .box2{
    flex: .5 1;
    background-color: #f98909;
    transform: translateY(-15vh);
    z-index: 1;
    transition: .5s;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.align .boxs:hover .box1{
    transform: translateY(0);
    transition: .5s;
}
.align .boxs:hover .box2{
    transform: translateY(0);
    transition: .5s;
}
.align .boxs .box2 h1{
    color: white;
    margin: 2vw;
    font-size: 1.5vw;
}
.box1 img{
    width: 25vw;

}
.box2 a{
    position: absolute;
    bottom: 1vw;
    right: 1vw;
    padding: .4vw;
    background-color: white;
    color: #f98909;
    border-radius: 25px;
    text-decoration: none;
}
.box2 h4{
    font-size: 1.1vw;
}

.logo{
    position: absolute;
    left: 10vw;

}
.logo img{
    width: 80vw;
    animation: blink 2s;
    opacity: .2;
}
@keyframes blink{
    from{
        opacity: 1;
    }to{
        opacity: .2;
    }
}
@keyframes reversBlink{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
