.App{
  margin: 0;
  padding: 0;
  font-family: 'Roboto',sans-serif;
  overflow: hidden;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #3d3d3d; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.Main{
  width: 100vw;
  height: 100vh;
  background-color: #252827;
  display: flex;
  flex-direction: row;
}

.main_screen{
  margin: 10vh 10vh;
  width: 80vw;
  height: 80vh;
  background-color: #3d3d3d;
  overflow-y: scroll;
  box-shadow: 0 5px 5px 0  rgba(255, 255, 255, 0.596);
}

.nav{
  width: 20vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  
  align-items: flex-start;
  justify-content: center;
}
.nav .navlink{width: 20vw;
  display: flex;
  flex-direction: row;
  justify-content: start;
  margin:15px;
  cursor: pointer;
}
.nav .dot{
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: white;
  transition: .5s;
  opacity: .2;
  transform: translateY(25%);
}
.nav .navlink h1{
  margin: 0 15px;
  opacity: .2;
  transition: .5s;
  font-size: 1.6vw;
  color: #f98909;
  text-align: left;
}
.nav .navlink:hover .dot,.nav .navlink:hover h1,
.nav .navlink.active .dot,.nav .navlink.active h1{
  opacity: 1;
  transition: .5s;
}
.main_screen .not_found{
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.main_screen h3{
  color: white;
}
.height_50{
  height: 40vh;
  display: flex;
  align-items: center;
}

.campInfo h4{
  color: #f98909;
}
.campInfo h5{
  color: white;
}
.campInfo i{
  width: 100px;
  height: 100px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.campInfo{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  
}
.speaker h4{
  color: #f98909;
}
.speaker h5{
  color: white;
}
.speaker{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.speaker img{
  width: 12vw;
  height: 12vw;
  border-radius: 2vw;
}
.sponsor a{
  color: white;
  text-decoration: none;
  
}
.contact {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  
}
.contact i{
  cursor: pointer;
  opacity: .3;
  color: white;
}
.contact i:hover{
  
  opacity: 1;
}
.logos{
  position: absolute;
  top: 0;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  
}
.logos img{
  width: 10vw;
}
